<template>
  <v-data-table
    mobile-breakpoint="0"
    :headers="header"
    :items="ROUND.teams"
    disable-pagination
    hide-default-footer
    class="elevation-3"
    fixed-header
    height="calc(100vh - 180px)"
  >
    <template v-for="(question, questionNumber) in ROUND.questions" v-slot:[slot_name(questionNumber)]="{ item }">
      <v-menu
        :key="questionNumber"
        transition="scroll-y-transition"
      >
        <template v-slot:activator="{ on }">
          <v-chip
            :key="questionNumber"
            v-on="on"
            :outlined="getAnswerColor(item.answers[questionNumber], ROUND.questions[questionNumber].answers, ROUND.questions[questionNumber].wrong_answers, ROUND.default_question_type) === 'red'"
            :text-color="getAnswerColor(item.answers[questionNumber], ROUND.questions[questionNumber].answers, ROUND.questions[questionNumber].wrong_answers, ROUND.default_question_type)"
            :color="getAnswerColor(item.answers[questionNumber], ROUND.questions[questionNumber].answers, ROUND.questions[questionNumber].wrong_answers, ROUND.default_question_type) !== 'red' ? 'transparent' : 'red'"
          >
            {{ item.answers[questionNumber] }}
          </v-chip>
        </template>
        <v-card class="pa-3">
          {{ item.answers[questionNumber] }}
          <div class="text-center">
            <v-btn class="mx-2" icon color="blue" @click="addRightAnswer(roundNumber, questionNumber, item.answers[questionNumber])" >
              <v-icon>mdi-thumb-up</v-icon>
            </v-btn>
            <v-btn class="mx-2" icon color="red" @click="addWrongAnswer(roundNumber, questionNumber, item.answers[questionNumber])">
              <v-icon>mdi-thumb-down</v-icon>
            </v-btn>
          </div>
        </v-card>
      </v-menu>
    </template>
  </v-data-table>
</template>

<script>
export default {
  name: 'Table',

  props: {
    roundNumber: {
      type: Number
    }
  },

  data: () => ({
    header: [{
      text: 'Team',
      value: 'team_name'
    }]
  }),

  mounted () {
    for (let i = 0; i < this.ROUND.questions.length; i++) {
      this.header.push({
        text: `Q${i + 1}`,
        value: `answers[${i}]`
      })
      if (!Object.prototype.hasOwnProperty.call(this.ROUND.questions[i], 'wrong_answers')) this.ROUND.questions[i].wrong_answers = []
    }
  },

  methods: {
    slot_name (n) {
      return `item.answers[${n}]`
    },
    getAnswerColor (answer, rightAnswers, wrongAnswers, type) {
      if (rightAnswers.some(rightAnswer => rightAnswer === answer)) return 'black'
      else if (type === 'close') return 'grey'
      else if (wrongAnswers.some(wrongAnswer => wrongAnswer === answer)) return 'grey'
      else return 'red'
    },
    addRightAnswer (roundNumber, questionNumber, answer) {
      this.$store.dispatch('ADD_ANSWER', { roundNumber, questionNumber, answer })
      this.$forceUpdate()
    },
    addWrongAnswer (roundNumber, questionNumber, answer) {
      this.$store.dispatch('ADD_WRONG_ANSWER', { roundNumber, questionNumber, answer })
      this.$forceUpdate()
    }
  },

  computed: {
    ROUND () {
      return this.$store.getters.ROUND(this.roundNumber)
    }
  }
}
</script>

