<template>
  <v-card class="elevation-3 align-center d-flex flex-row justify-center flex-wrap" max-height="calc(100vh - 180px)" style="overflow-y: scroll; overflow-x: hidden">
    <v-card
      v-for="(question, questionNumber) in ROUND.questions"
      :key="questionNumber"
      class="ma-3"
      width="300"
    >
      <v-card-text>
        <div class="title text--primary text-center">
          Question {{ questionNumber + 1 }}
        </div>
        <p class="font-weight-light overline text-center">{{ ROUND.name }}</p>
        <div><span class="text--primary font-weight-bold">Hint:</span> {{ question.hint }} </div>
        <p><span class="text--primary font-weight-bold">Right answer:</span> {{ question.answers[0] }} </p>
      </v-card-text>
      <v-spacer></v-spacer>
      <v-card-actions>
        <v-btn icon color="blue" :disabled="!answers[questionNumber].length" @click="addRightAnswer(roundNumber, questionNumber, answers[questionNumber][0])">
          <v-icon>mdi-thumb-up</v-icon>
        </v-btn>
        <v-spacer/>
        <transition name="scroll-y-transition" mode="out-in">
        <v-badge
          color="red"
          :content="answers[questionNumber].length"
          :value="answers[questionNumber].length"
          overlap
          bordered
          :key="answers[questionNumber][0]"
        >
            <v-chip v-if="answers[questionNumber].length"> {{ answers[questionNumber][0] }} </v-chip>
        </v-badge>
        </transition>
        <v-spacer/>
        <v-btn icon color="red" :disabled="!answers[questionNumber].length" @click="addWrongAnswer(roundNumber, questionNumber, answers[questionNumber][0])">
          <v-icon>mdi-thumb-down</v-icon>
        </v-btn>
      </v-card-actions>
    </v-card>
