  </v-card>
</template>

<script>
export default {
  name: 'Smart',

  data: () => ({
    answers: []
  }),

  props: {
    roundNumber: {
      type: Number
    }
  },

  mounted () {
    for (let i = 0; i < this.ROUND.questions.length; i++) {
      const question = this.ROUND.questions[i]
      if (!Object.prototype.hasOwnProperty.call(question, 'wrong_answers')) question.wrong_answers = []
      this.answers.push([])
      if (this.ROUND.default_question_type !== 'close' && Object.prototype.hasOwnProperty.call(this.ROUND, 'teams')) {
        for (const team of this.ROUND.teams) {
          if (!question.answers.some(rightAnswer => rightAnswer === team.answers[i])) {
            if (!question.wrong_answers.some(wrongAnswer => wrongAnswer === team.answers[i])) {
              if (!this.answers[this.answers.length - 1].some(answer => answer === team.answers[i])) {
                this.answers[this.answers.length - 1].push(team.answers[i])
              }
            }
          }
        }
      }
    }
  },

  methods: {
    addRightAnswer (roundNumber, questionNumber, answer) {
      this.$store.dispatch('ADD_ANSWER', { roundNumber, questionNumber, answer })
      this.answers[questionNumber].shift()
      this.$forceUpdate()
    },
    addWrongAnswer (roundNumber, questionNumber, answer) {
      this.$store.dispatch('ADD_WRONG_ANSWER', { roundNumber, questionNumber, answer })
      this.answers[questionNumber].shift()
      this.$forceUpdate()
    }
  },

  computed: {
    ROUND () {
      return this.$store.getters.ROUND(this.roundNumber)
    }
  }
}
</script>

