import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    model: {
    },
    tableView: false,
    loadingModel: false
  },
  getters: {
    MODEL: state => {
      return state.model
    },
    ROUND: state => roundNumber => {
      return state.model.rounds[roundNumber]
    },
    ANSWERS: state => (roundNumber, questionNumber) => {
      return state.model.rounds[roundNumber].questions[questionNumber].answers || []
    },
    WRONG_ANSWERS: state => (roundNumber, questionNumber) => {
      return state.model.rounds[roundNumber].questions[questionNumber].wrong_answers || []
    },
    TABLE_VIEW: state => {
      return state.tableView
    },
    LOADING_MODEL: state => {
      return state.loadingModel
    }
  },
  mutations: {
    DELETE_ANSWER (state, { roundNumber, questionNumber, index }) {
      state.model.rounds[roundNumber].questions[questionNumber].answers.splice(index, 1)
    },
    DELETE_WRONG_ANSWER (state, { roundNumber, questionNumber, index }) {
      state.model.rounds[roundNumber].questions[questionNumber].wrong_answers.splice(index, 1)
    },
    ADD_ANSWER (state, { roundNumber, questionNumber, answer }) {
      state.model.rounds[roundNumber].questions[questionNumber].answers.push(answer)
    },
    ADD_WRONG_ANSWER (state, { roundNumber, questionNumber, answer }) {
      if (!Object.prototype.hasOwnProperty.call(state.model.rounds[roundNumber].questions[questionNumber], 'wrong_answers')) {
        state.model.rounds[roundNumber].questions[questionNumber].wrong_answers = []
      }
      state.model.rounds[roundNumber].questions[questionNumber].wrong_answers.push(answer)
    },
    SET_TABLE_VIEW (state, payload) {
      state.tableView = payload.state
    },
    SET_LOADING_MODEL (state, payload) {
      state.loadingModel = payload.state
    },
    SET_MODEL (state, payload) {
      // state.model.rounds.splice(0, state.model.rounds.length)
      state.model = payload.model
    }
  },
  actions: {
    ADD_ANSWER (context, { roundNumber, questionNumber, answer }) {
      const wrongAnswers = context.getters.WRONG_ANSWERS(roundNumber, questionNumber)
      const index = wrongAnswers.indexOf(answer)
      if (index !== 1) {
        context.commit('DELETE_WRONG_ANSWER', {
          roundNumber,
          questionNumber,
          index
        })
      }
      const answers = context.getters.WRONG_ANSWERS(roundNumber, questionNumber)
      if (!answers.some(rightAnswer => rightAnswer === answer)) {
        context.commit('ADD_ANSWER', { roundNumber, questionNumber, answer })
      }
    },
    ADD_WRONG_ANSWER (context, { roundNumber, questionNumber, answer }) {
      const answers = context.getters.ANSWERS(roundNumber, questionNumber)
      const index = answers.indexOf(answer)
      if (index > 0) {
        context.commit('DELETE_ANSWER', {
          roundNumber,
          questionNumber,
          index
        })
      }
      if (index !== 0) {
        const wrongAnswers = context.getters.WRONG_ANSWERS(roundNumber, questionNumber)
        if (!wrongAnswers.some(wrongAnswer => wrongAnswer === answer)) {
          context.commit('ADD_WRONG_ANSWER', { roundNumber, questionNumber, answer })
        }
      }
    },
    async GET_MODEL (context) {
      try {
        context.commit('SET_LOADING_MODEL', { state: true })
        const { data: model } = await axios.get('/model')
        const { data: answers } = await axios.get('/model')
        console.log(model)
        console.log(answers)
        context.commit('SET_MODEL', { model: model })
      } catch (error) {
        console.log(error.response)
      }
      context.commit('SET_LOADING_MODEL', { state: false })
    }
  }
})
